<template>

  <print-pdf-layout>
    <pdf-header slot="header">報價單</pdf-header>
    
    <pdf-price-member v-if="membery!=null" :member="membery" slot="membery"></pdf-price-member>

    <pdf-price-msg 
      slot="msg"
      v-if="membery!=null"

      :member="membery" 
      :pricesTime="prices_timed" 
      :pricesNum="prices_numed" 
    >
    </pdf-price-msg>

    <pdf-prices-freight :freights="prices_content" v-if="this.prices_content.length > 0" slot="freights"></pdf-prices-freight>
  
    <pdf-footer :member="membery" v-if="membery!=null" slot="footer"></pdf-footer>
  
  </print-pdf-layout>

</template>

<script>
import conn from '@/service/conn'
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import config from '@/common/config'

import PdfHeader from '@/components/Pdf/PdfHeader.vue'
import PdfFooter from '@/components/Pdf/PdfFooter.vue'

import PdfPriceMember from '@/components/Pdf/Prices/PdfPriceMember.vue'
import PdfPriceMsg from '@/components/Pdf/Prices/PdfPriceMsg.vue'
import PdfPricesFreight from '@/components/Pdf/Prices/PdfPricesFreight.vue'

import Loading from '@/components/Shimmer/Loading.vue'
import PrintPdfLayout from '@/components/Pdf/Print/PrintPdfLayout.vue'

export default {
  name: 'PrintPricesPdf',
  components: { 
    PdfFooter,
    PdfHeader,
    PdfPriceMember,
    PdfPriceMsg,
    PdfPricesFreight,
    Loading,
    PrintPdfLayout
  },
  data() {
    return {
      pcc_id: 0,
      membery: null,
      prices: null,
      prices_content: [],

      prices_timed: null,
      prices_numed: null
    }
  },
  mounted() {
    const id = this.$route.query.pcc_id


    conn.prices_content.getOne(id).then( res => {
      
      this.prices = res.price_collect
      this.prices_timed = tool.timed.ser_timed(this.prices.start_time, false, true)
      this.prices_numed = res.price_collect.num

      this.membery = this.prices.membery
      this.prices_content = JSON.parse(res.content)

      setTimeout(() => {
        print()
      }, 800)
    })
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


